import React, { useState, useEffect } from "react";
import "./App.css";
import Button from "@mui/material/Button";
import { FormControl, InputLabel, Input } from "@mui/material";
import Todo from "./Todo";
import db from "./firebase";
import firebase from "firebase/compat/app";
function App() {
  const [todos, setTodos] = useState([]);
  const [input, setInput] = useState(" ");
  //when the app loads, we need to listen to the database and fetch new todos as they get added/removed
  useEffect(() => {
    //this code here ... fires when the app loads and db.collection("collection name").onSnapshot(()) .this will pull data are on fieship db
    db.collection("todos")
      .orderBy("timestamp", "desc")
      .onSnapshot((snapshot) => {
        setTodos(snapshot.docs.map((doc) => doc.data().todo));
        // console.log(snapshot.docs.map((doc) => doc.data().todo));
      });
  }, []);

  console.log(input);
  const addTodo = (event) => {
    //and preventing from auto submiting
    event.preventDefault();
    //this will fire off when we click the button

    //this db.collection("collection name").add() will add what we puting here on browser
    db.collection("todos").add({
      todo: input,
      timestamp: firebase.firestore.FieldValue.serverTimestamp(),
    });
    // console.log("I am working button clicked");
    setTodos([...todos, input]);
    setInput(""); //clear up the input after hitting todo button
  };
  return (
    <div className="App">
      <h1>Todo App </h1>
      <form>
        <FormControl>
          <InputLabel>✔ write a Todo</InputLabel>
          <Input
            value={input}
            onChange={(event) => setInput(event.target.value)}
          />
        </FormControl>
        <Button
          disabled={!input}
          onClick={addTodo}
          type="submit"
          variant="outlined"
        >
          Add todo
        </Button>
      </form>
      <ul>
        {todos.map((todo) => (
          <Todo text={todo} />
        ))}
      </ul>
    </div>
  );
}

export default App;
